body {
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer {
  background-color: #000000;
  color: #D9D9D9;
  margin-top: 5rem;
  font-size: 2vw;
  padding: 1rem;
}

#navbar_anchor {
  font-weight: bold;
  border-style: none;
  background-color: #000000;
}
#navbar_anchor2 {
  font-weight: bold;
  margin-right: 4rem;
  border-style: none;
  background-color: #000000;
}

#container {
  position: relative;
  text-align: center;
  color: white;
}

#container {
  width: 100%;
  position: relative;
  overflow: hidden;
}

#container:after {
  content: "";
  position: absolute;
  top: 93%;
  left: 0;
  height: 100%;
  width: 150%;
  background: rgb(255, 255, 255);
  -webkit-transform: rotate(-3deg);
  transform: rotate(-3deg);
}

#store-img {
  width: 100%;
}

#btnAddProduct {
  position: absolute;
  top: 35%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #DAC8B0;
  font-weight: bold;
  font-size: 3vw;
  background-color: #000000;
  border-style: none;
  width: 45%;
  height: 13%;
  transition: 0.1s;
}

#btnAddProduct:hover {
    background-color: #131313;
}

#col_brand {
  margin-right: -18px;
}

#navBarMain {
  background-color: #000000;
  color: #DAC8B0;
}

#navBarTitle {
  font-weight: bolder;
  color: #DAC8B0;
}

#row-brand {
  flex-wrap: nowrap;
}

#col_navbar_a {
  width: 0;
}

#products-header {
  font-weight: bolder;
  font-size: 5vw;
  margin-bottom: 2rem;
}

.table-container {
  margin-left: auto;
  margin-right: auto;
  border-radius: 12px;
  width: 80%;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.49);
    -webkit-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.49);
    -moz-box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.49);
}

#products-tr:nth-child(odd) {
  background-color: #D9D9D9;
}

#products-tr:nth-child(even) {
  background-color: #ffffff;
}

thead {
  background-color: #ffffff;
}

.table-header {
  background-color: #DAC8B0;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

#table-header-text {
  font-weight: bold;
  text-align: start;
  font-size: medium;
  padding: 1rem;
}

#btn-edit{
  border-radius: 100px;
  background-color: #DAC8B0;
  color: #000000;
}

#btn-authorize{
  border-radius: 100px;
  background-color: #2bc52b;
  color: #ffffff;
}

#btn-trash{
  border-radius: 100px;
  background-color: #DAB0B0;
  color: #000000;
}

#price::-webkit-outer-spin-button,
#price::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

#price[type=number] {
  -moz-appearance: textfield;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#employees-table {
    position: absolute;
    top: 35%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 80%;
}
